@import '../../base';

%standard-font {
  font-family: $font-open-sans;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white;
}
$heavy-weight: 700;
$horizontal-anim-duration: 90s;
$vertical-anim-duration: 4.5s;

.tos {
  position: relative;
  overflow: hidden;

  .title-container {
    max-width: 1000px;
    text-align: center;
    margin: auto;
    padding: 10px;

    .title {
      @extend %standard-font;
      display: inline;
      font-size: 20px;
      font-weight: $heavy-weight;
      color: $color-title !important;

      @media screen and (min-width: 1440px) {
        font-size: 34px;
      }
    }

    .desc {
      text-align: center;
      @extend %standard-font;
      font-size: $font-size-small;
      font-weight: $font-weight-normal;
      color: $color-title !important;

      @media screen and (min-width: 1440px) {
        font-size: 19.5;
      }
    }
  }
  .content {
  
    .content-row {
      justify-content: center;
      margin-bottom: 50px;
      padding: 20px;
  
      @media screen and(min-width: 768px){
        padding: 40px;
      }
    }
  
    .title {
      @extend %standard-font;
      font-size: $font-size-small;
      font-weight: $font-weight-bold;
      color: $color-title;
      text-align: left !important;
      width: 80vw;
      margin: auto;
  
      @media screen and (min-width: 1440px) {
        font-size: $font-size-small;
      }
    }
  
    .title-desc {
      @extend %standard-font;
      font-size: $font-size-smallest;
      color: $color-body;
      text-align: left !important;
      padding: 10px 0;
      width: 80vw;
      margin: auto;
  
      @media screen and (min-width: 1440px) {
        font-size: $font-size-small;
      }
  
      b {
        font-weight: $font-weight-bold;
      }
    }
  
    ul {
      list-style: none;
      padding: 0;
  
      li {
        padding: 10px 0;
      }
    }
    .title-letter {
      @extend %standard-font;
      font-size: $font-size-smallest;
      color: $color-body;
      text-align: left !important;
      padding: 10px;
      width: 80vw;
      margin: auto;
  
      @media screen and (min-width: 1440px) {
        font-size: $font-size-small;
      }
  
      b {
        font-weight: $font-weight-bold;
      }
    }
  
    ul {
      list-style: none;
      padding: 0;
  
      li {
        padding: 10px 0;
      }
    }
  }
}